@media (max-width: 812px) {
    .section_header {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 3;
        &_content {
            background-color: #fff;
            z-index: 6; }
        &_btn_book_now {
            position: absolute;
            left: 50%;
            transform: translate(-50%); }
        .mobile_menu_wrapper {
            display: block;
            position: absolute;
            width: 50px;
            height: 50px;
            right: 25px;
            top: 25px;
            z-index: 5; }
        .mobile_menu {
            position: absolute;
            right: 10px;
            top: 25px;
            width: 30px;
            height: 2px;
            background-color: #57616b;
            transition: .3s linear;
            &::before,
            &::after {
                content: '';
                position: absolute;
                right: 0;
                width: 30px;
                height: 2px;
                background-color: #57616b;
                z-index: 5;
                transition: .3s linear; }
            &::after {
                transform: translateY(10px); }
            &::before {
                transform: translateY(-10px); } }
        .mobile_menu.active {
            transform: translateX(-50px);
            background-color: transparent;
            &::after {
                transform: rotate(45deg) translate(25px, -25px); }
            &::before {
                transform: rotate(-45deg) translate(25px, 25px); } }
        a.section_header_nav_link {
            margin: 0;
            font-size: 20px;
            padding: 20px 0;
            width: 100%;
            display: block;
            border-bottom: 2px solid #fafcfd; }
        .section_header_nav_item:nth-child(3) {
            a {
                padding: 20px 0; } }
        &_wrapper {
            position: absolute;
            top: 95px;
            right: -100%;
            width: 100%;
            background-color: #fafcfd;
            height: 330px;
            box-shadow: 0px 0px 7px 0px #ccc;
            transition: .3s linear; }
        &_wrapper.active {
            right: 0; }
        &_nav {
            height: 100%;
            &_list {
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                height: 100%; }
            &_item {
                width: 100%;
                text-align: center; } } } }
